export default{
  data() {
    return {
      title: '',
      type: '',
      showDel: false,
      code_word: '获取验证码',
      username: '',
      code: '',
      check_code: '',
      showInfo: false
    }
  },
  onLoad(option) {
    this.type = option.type
    if(option.type === 'register') {
      this.title = '注册'
      return
    }
    if(option.type === 'forget') {
      this.title = '找回密码'
      return
    }
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    onInput(e) {
      let value = e.detail.value
      if(value) {
        this.showDel = true
        return
      }
      this.showDel = false
    },
    goNext() {
      console.log(this.check_code, this.code, this.check_code === this.code)
      if(!this.check_code) {
        uni.showToast({
          title: '请先获取手机验证码',
          icon: 'none'
        })
        return
      }
      if(this.check_code !== this.code) {
        uni.showToast({
          title: '验证码不正确',
          icon: 'none'
        })
        return
      }
      console.log(this.check_code, this.code)
      if(this.check_code === this.code && this.check_code) {
        uni.navigateTo({
          url: 'password?type=' + this.type + '&mobile=' + this.username,
          "animationType": "zoom-fade-out"
        })  
      }
    },
    goLogin() {
      uni.navigateTo({
        url: 'login',
        "animationType": "zoom-fade-out"
      })
    },
    getCode() {
      let value = /^1[3456789]\d{9}$/.test(this.username)
      let that = this
      if (!value){
        uni.showToast({
          title: '请输入正确的手机号码',
          icon: 'none'
        })
        return
      }
      
      // console.log(this.username)
      // let num = 1111
      // that.check_code = num.toString()
      // console.log(num.toString())
      
      this.$http({
        url: this.$api.sendcode,
        method: 'POST',
        data: {
          mobile: this.username
        },
        cb: (err, res) => {
          if(!err && res.code === 1) {
            that.check_code = res.data.info.code.toString()
            that.code_word = 120
            that.showInfo = true
            setInterval(function() {
              if (that.code_word > 0) {
                that.code_word--
              }
              if (that.code_word === 0) {
                that.check_code = ''
                that.showInfo = false
                return
              }
            }.bind(that), 1000)
          } else {
            uni.showToast({
              title: '手机验证码获取失败',
              icon: 'none'
            })
          }
        }
      })
    }
  }
}
